import React, { Component } from "react";
import { Modal, Col, Row } from "antd";
import "../modal/modal.scss";
import {
  SHOW,
  CLOSE_TEXT_MODAL,
  CUG_FIELDS,
  NO_CUG_FIELDS,
} from "../../headings";

class CugModal extends Component {
  state = {
    visible: false,
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <>
        <a onClick={this.showModal}>{SHOW}</a>
        {this.props.selectedRows.cug_filter &&
        Object.keys(this.props.selectedRows.cug_filter).length > 0 ? (
          <Modal
            className="modal-checkbox"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={590}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={CLOSE_TEXT_MODAL}
          >
            <div className="font-18">{CUG_FIELDS}</div>

            <Row className="mb-2 autocomplete">
              <Col span={24} className="mt-4">
                Location:
                <span className="font-weight-normal pl-2">Kolkata</span>
              </Col>
            </Row>
          </Modal>
        ) : (
          <Modal
            className="modal-checkbox"
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            centered
            width={590}
            okButtonProps={{
              children: "Custom OK",
            }}
            cancelButtonProps={{ style: { display: "none" } }}
            okText={CLOSE_TEXT_MODAL}
          >
            <div>{NO_CUG_FIELDS}</div>
          </Modal>
        )}
      </>
    );
  }
}

export default CugModal;
